import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { JustWatchResponse, MySave, Favs } from '../interfaces/interfaces';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { AlertController, ModalController } from '@ionic/angular';
import { LoginPage } from '../auth/pages/login/login.page';
import { AuthService } from '../auth/auth.service';

const URL = "https://api.movin.tk/api";

@Injectable({
  providedIn: 'root'
})
export class JustwatchService {

  private httpHeader = new HttpHeaders({'Content-Type':'application/json'})

  constructor(private http: HttpClient,
    public alertController: AlertController,
    private modalController: ModalController,
    private authService: AuthService) { }

  private addAuthHeaders(): HttpHeaders{
    let token = this.authService.token;
    if(token != null){
      return this.httpHeader.append('Authorization','Bearer ' + token)
    } else {
      return this.httpHeader
    }
  }

  getPlatforms(body): Observable <Array<JustWatchResponse>>{
    return this.http.post<Array<JustWatchResponse>>(URL + '/search', body)
  }

  saveFavorite(mySave: MySave): Observable <any>{
    return this.http.post<Array<JustWatchResponse>>(URL + '/mysave', mySave, {headers: this.addAuthHeaders()}).pipe(
      catchError( err => {
          if(err.status == 401) this.presentAlertAuthErr()
          return throwError(err)
      })
    )
  }

  getFavorites(){
    return this.http.get<Favs>(URL + '/mysave', {headers: this.addAuthHeaders()})
  }

  async presentAlertAuthErr() {
   const alert = await this.alertController.create({
     cssClass: 'my-custom-class',
     header: 'Usuario sin identificar!',
     message: 'Por favor, inicie sesión para guardar contenido...',
     buttons: [
       {
         text: 'Cancel',
         role: 'cancel',
         cssClass: 'secondary'
       }, {
         text: 'Iniciar Sesión',
         handler: () => {
           this.login()
         }
       }
     ]
   });

   await alert.present();
 }

 async login() {
   const loginOptionsModal = await this.modalController.create({
     component: LoginPage,
   });
   return await loginOptionsModal.present();
 }
}
