import { Component, OnInit, HostListener } from '@angular/core';
import {Location} from '@angular/common';
import { TmdbService } from 'src/app/services/tmdb.service';
import { FilmDetail, SerieDetail, Cast, Crew, JustWatchPostBody, JustWatchResponse, MySave } from 'src/app/interfaces/interfaces';
import { ModalController } from '@ionic/angular';
import { JustwatchService } from 'src/app/services/justwatch.service';
import { ActivatedRoute } from '@angular/router';

const screenWidthBreakPoint = 1234;
const screenWidthBreakPointMid = 720;

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss'],
})
export class DetailComponent implements OnInit {

  screenWidth: number;

  id: number;
  type: string;
  title: string;
  mySave: MySave = {
    idtmdb: 0
  };

  film: FilmDetail;
  serie: SerieDetail;
  actors: Cast[] = [];
  director: Crew;
  oculto: number = 150;
  justWatchIsLoading: boolean = false;
  justWatchPostBody: JustWatchPostBody = {
    country: 'ES',
    search: '',
    idtmdb: 0,
    type: '',
    tmdb_vote: 0,
    tmdb_popularity: 0
  };
  justWatchProviders: Array <JustWatchResponse> = [];
  rentProviders: Array <JustWatchResponse> = [];
  buyProviders: Array <JustWatchResponse> = [];
  streamingProviders: Array <JustWatchResponse> = [];

  slideOptActores = {};
  slideOptProviders = {};

  constructor(private tmdb: TmdbService,
    private justWatch: JustwatchService,
    private route: ActivatedRoute,
    private _location: Location) {}

  ngOnInit() {
    this.getParams();
    this.loadJustWatchPostBody();
    this.slideOptActores = {
      slidesPerView: 2.8,
      centeredSlides: true,
      centeredSlidesBounds: true,
      freeMode: true
    }
    this.slideOptProviders = {
      slidesPerView: 2.7,
      scrollbar: true,
      centeredSlides: true,
      centeredSlidesBounds: true,
      freeMode: true
    }
    this.getScreenSize();
    this.loadDetails();
  }

  getParams() {
    this.id = Number(this.route.snapshot.paramMap.get("id"))
    this.type = this.route.snapshot.paramMap.get("type")
    this.title = this.route.snapshot.paramMap.get("title")
  }

  loadDetails(): void{
    if(this.type == "movie"){
      this.tmdb.getMovieDetail(String(this.id)).subscribe( res => {
        this.film = res;
      })
      this.tmdb.getMovieCredits(String(this.id)).subscribe( res => {
        this.actors = res.cast;
        let crew = res.crew;
        this.director = crew.filter(element => element.job == "Director")[0];
        console.log(this.director)
      })
    } else if (this.type == "tv"){
      this.tmdb.getSerieDetail(String(this.id)).subscribe( res => {
        this.serie = res;
      })
      this.tmdb.getSerieCredits(String(this.id)).subscribe( res => {
        this.actors = res.cast;
      })
    }
  }

  loadJustWatchPostBody(): void {
    this.justWatchPostBody.idtmdb = this.id
    this.justWatchPostBody.search = this.title;
    this.justWatchPostBody.type = this.type;

    this.justWatchIsLoading = true;
    this.justWatchProviders = [];
    this.justWatch.getPlatforms(this.justWatchPostBody).subscribe(res => {
      if(res == null ){
        this.justWatchIsLoading = false;
      } else {
        this.justWatchProviders = res;
        this.buyProviders = this.justWatchProviders.filter(e => { return e.monetization_type == "buy"})
        this.rentProviders = this.justWatchProviders.filter(e => { return e.monetization_type == "rent"})
        this.streamingProviders = this.justWatchProviders.filter(e => { return e.monetization_type == "flatrate"})
        this.justWatchIsLoading = false;
      }
    })
  }

  @HostListener('window:resize', ['$event'])
  getScreenSize(event?) {
    this.screenWidth = window.innerWidth;
    if (this.screenWidth > screenWidthBreakPoint){
      this.slideOptActores = {
        slidesPerView: 4.3,
        centeredSlides: true,
        centeredSlidesBounds: true,
        freeMode: true
      }
    } else if (this.screenWidth > screenWidthBreakPointMid) {
      this.slideOptActores = {
        slidesPerView: 3.4,
        centeredSlides: true,
        centeredSlidesBounds: true,
        freeMode: true
      }
    } else {
      this.slideOptActores = {
        slidesPerView: 2.8,
        centeredSlides: true,
        centeredSlidesBounds: true,
        freeMode: true
      }
    }
  }

  return(): void{
    this._location.back();
  }

  save(){
    console.log(this.id)
    console.log(this.serie)
    console.log(this.film)
    this.mySave.idtmdb = this.id;
    this.justWatch.saveFavorite(this.mySave).subscribe(res=>{
      console.log(res)
    })
  }

}
